import React from 'react';
import './FaceRecognition.css';

const FaceRecognition = ({ imageUrl, box }) => {
	return (
		<div className='center ma'>
			<div className='absolute mt2'>
				<img id='image-input' src={imageUrl} alt="FaceImage" width='500px' height='auto'/>
				<div className="bounding-box allow-pointer-events" style={{top: box.topRow, right: box.rightCol, bottom: box.bottomRow, left: box.leftCol}}>
					
				</div>
			</div>
		</div>
	)
}
 



export default FaceRecognition;